.brands-history {
  position: relative;
  z-index: 10;

  &__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: $mobile-max) {
      flex-direction: column;
      justify-content: flex-start;
    }
  }

  &__content {
    max-width: 490px;

    @media (max-width: $tablet-max) {
      max-width: 320px;
    }

    @media (max-width: $mobile-max) {
      margin: 48px auto 0;
      text-align: center;
    }
  }

  &__logo {
    width: 385px;
    height: 107px;
    margin: 0 112px 0 0;

    @media (max-width: $tablet-max) {
      width: 229px;
      height: 63px;
      margin: 0 64px 0 0;
    }

    @media (max-width: $mobile-max) {
      margin: 0 auto;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
  }

  &__title {
    font-size: 30px;
    line-height: 35px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 1px;

    @media (max-width: $tablet-max) {
      font-size: 18px;
      line-height: 24px;
    }
  }

  &__desc {
    margin: 32px 0 0;
    font-size: 18px;
    line-height: 21px;

    @media (max-width: $tablet-max) {
      font-size: 14px;
      line-height: 18px;
    }

    p {
      &:not(:first-child) {
        margin: 16px 0 0;
      }
    }
  }

  &__static {
    max-width: 950px;
    margin: 54px auto 0;

    @media (max-width: $tablet-max) {
      max-width: 630px;
      margin: 40px auto 0;
    }

    @media (max-width: $mobile-max) {
      margin: 24px auto 0;
    }

    img,
    p {
      &:first-child {
        margin-top: 0;
      }
    }

    img {
      margin: 54px 0 72px;

      @media (max-width: $tablet-max) {
        margin: 52px auto 32px;
      }

      @media (max-width: $mobile-max) {
        margin: 24px auto;
      }
    }

    p {
      margin: 16px 0;
      font-size: 18px;
      line-height: 21px;

      @media (max-width: $tablet-max) {
        font-size: 14px;
        line-height: 18px;
      }

      @media (max-width: $mobile-max) {
        text-align: center;
      }
    }
  }

